import React from 'react';
import Head from 'next/head';

import styled from 'styled-components';

import '../css/artist.css';
import '../css/normalize.css';
import '../css/skeleton.css';

class Auditory extends React.Component{

	
	constructor(props){

	    super(props)

	    let artist = props.artist
	    if(artist.code === "abcd"){;
			console.log('match');
			this.state = {
				image:artist.display_src,
				name:artist.name,
				mv:artist.mv,
				bio:artist.bio
			}
		}   
		console.log(this);
	}

	render(){

		return(
			<div className='artistArea'>
				<div className='artistBackground'>
					<img src={this.state.image} className='artistMainPhoto' />	    
					<iframe className='artistVideo' src={this.state.mv} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
					<div className='bio'>
					{this.state.bio}
					</div>
				</div>
			</div>
		)
	}
}

export default Auditory;